 */.hamburger{padding:20px 15px;display:inline-block;cursor:pointer;transition-property:opacity, filter;transition-duration:0.15s;transition-timing-function:linear;box-shadow:none;font:inherit;color:inherit;text-transform:none;background-color:transparent;border:0;margin:0;overflow:visible}.hamburger:focus{outline:none}.hamburger:active{-webkit-tap-highlight-color:transparent;box-shadow:0}.hamburger:hover{box-shadow:0;opacity:1}.hamburger.is-active:hover{opacity:1;box-shadow:0}.hamburger.is-active .hamburger-inner,.hamburger.is-active .hamburger-inner::before,.hamburger.is-active .hamburger-inner::after{background-color:#fff}.hamburger-box{width:30px;height:21px;display:inline-block;position:relative}.hamburger-inner{display:block;top:50%;margin-top:-1.5px}.hamburger-inner,.hamburger-inner::before,.hamburger-inner::after{width:30px;height:3px;background-color:#fff;border-radius:4px;position:absolute;transition-property:transform;transition-duration:0.15s;transition-timing-function:ease}.hamburger-inner::before,.hamburger-inner::after{content:"";display:block}.hamburger-inner::before{top:-9px}.hamburger-inner::after{bottom:-9px}.hamburger--emphatic{overflow:hidden}.hamburger--emphatic .hamburger-inner{transition:background-color 0.125s 0.175s ease-in}.hamburger--emphatic .hamburger-inner::before{left:0;transition:transform 0.125s cubic-bezier(0.6, 0.04, 0.98, 0.335),top 0.05s 0.125s linear,left 0.125s 0.175s ease-in}.hamburger--emphatic .hamburger-inner::after{top:9px;right:0;transition:transform 0.125s cubic-bezier(0.6, 0.04, 0.98, 0.335),top 0.05s 0.125s linear,right 0.125s 0.175s ease-in}.hamburger--emphatic.is-active .hamburger-inner{transition-delay:0s;transition-timing-function:ease-out;background-color:transparent !important}.hamburger--emphatic.is-active .hamburger-inner::before{left:-60px;top:-60px;transform:translate3d(60px, 60px, 0) rotate(45deg);transition:left 0.125s ease-out,top 0.05s 0.125s linear,transform 0.125s 0.175s cubic-bezier(0.075, 0.82, 0.165, 1)}.hamburger--emphatic.is-active .hamburger-inner::after{right:-60px;top:-60px;transform:translate3d(-60px, 60px, 0) rotate(-45deg);transition:right 0.125s ease-out,top 0.05s 0.125s linear,transform 0.125s 0.175s cubic-bezier(0.075, 0.82, 0.165, 1)}.hamburger--emphatic-r{overflow:hidden}.hamburger--emphatic-r .hamburger-inner{transition:background-color 0.125s 0.175s ease-in}.hamburger--emphatic-r .hamburger-inner::before{left:0;transition:transform 0.125s cubic-bezier(0.6, 0.04, 0.98, 0.335),top 0.05s 0.125s linear,left 0.125s 0.175s ease-in}.hamburger--emphatic-r .hamburger-inner::after{top:9px;right:0;transition:transform 0.125s cubic-bezier(0.6, 0.04, 0.98, 0.335),top 0.05s 0.125s linear,right 0.125s 0.175s ease-in}.hamburger--emphatic-r.is-active .hamburger-inner{transition-delay:0s;transition-timing-function:ease-out;background-color:transparent !important}.hamburger--emphatic-r.is-active .hamburger-inner::before{left:-60px;top:60px;transform:translate3d(60px, -60px, 0) rotate(-45deg);transition:left 0.125s ease-out,top 0.05s 0.125s linear,transform 0.125s 0.175s cubic-bezier(0.075, 0.82, 0.165, 1)}.hamburger--emphatic-r.is-active .hamburger-inner::after{right:-60px;top:60px;transform:translate3d(-60px, -60px, 0) rotate(45deg);transition:right 0.125s ease-out,top 0.05s 0.125s linear,transform 0.125s 0.175s cubic-bezier(0.075, 0.82, 0.165, 1)}.header{display:flex;justify-content:space-between;align-items:center;width:100%;padding:0 12.5rem;height:2.5rem;color:black;box-sizing:border-box;z-index:1;transition:all 0.3s ease;position:relative;box-shadow:0px 1px 10px #a8a8a8;z-index:2}.header::after{content:"";position:absolute;width:100%;height:100%;background-color:white;top:0;left:0;z-index:-1;pointer-events:none}.header .logo{display:flex;align-items:center}.header .logo h1{font-size:0.9rem;text-transform:uppercase}.header .logo img{margin-left:0.5rem;height:1rem;transition:all 0.3s ease}.header .menu-list{display:flex;align-items:center;list-style-type:none;font-size:0.5rem;margin:0}.header .menu-list li{margin:0 0.75rem;transition:all 0.3s ease}.header .menu-list li:hover{color:#369df1;cursor:pointer}.header .menu-list .round{background-color:#370057;padding:0.1rem 0.75rem;border-radius:1rem;border:solid 2px #3b003b}.header .menu-list .round:hover{background-color:transparent;border-color:white}.header .hamburger{display:none}@media only screen and (max-width: 1600px) and (max-height: 900px){.header{padding:0 10rem}.header .hamburger{filter:brightness(0)}}@media only screen and (max-width: 1440px) and (max-height: 1024px){.header{padding:0 8rem}}@media only screen and (max-width: 1024px) and (max-height: 1366px){.header{position:fixed;width:100%;padding:0 2.5rem;height:3.5rem;transition:all 0.5s ease;z-index:5}.header .menu-list{position:absolute;flex-direction:column;justify-content:flex-start;align-items:center;height:100vh;font-size:1rem;width:100%;color:white;right:0;top:0;z-index:1;pointer-events:none;transition:all 0.5s ease}.header .menu-list::after{content:"";position:absolute;width:100%;height:100%;top:0;left:0;background:#30b2da;opacity:0;transition:all 0.3s ease;z-index:-1}.header .menu-list li{opacity:0;margin:1.25rem 0}.header .menu-list .main-color{color:white;background-color:transparent;padding:0}.header .menu-list .main-color-background{color:white;background-color:transparent;padding:0;position:relative}.header .menu-list .main-color-background:hover{color:white;background-color:transparent}.header .menu-list-active{opacity:1;pointer-events:all}.header .hamburger{display:block;filter:brightness(0);z-index:5}.black-bgc{transition:all 0.5s ease}.black-bgc .menu-list::after{opacity:1}.black-bgc .hamburger{filter:none}.black-bgc .logo{opacity:0;transition:all 0.5s ease}}@media only screen and (max-width: 1024px) and (max-height: 1366px){.header .menu-list{padding:5rem 0;font-weight:bold}}@media only screen and (max-width: 414px) and (max-height: 823px){.header{padding:0.5rem;height:3rem;transition:all 0.5s ease}.header .menu-list{padding:0;padding-top:4rem;font-size:0.9rem;padding-bottom:6rem;transition:all 0.5s ease}}
